input,
textarea {
  width: 100%;
  margin: 0 auto 1rem auto;
  border: 1px solid #333;
  padding: 1rem;
  box-sizing: border-box;
  outline: none;
}
.login {
  border: 1px solid #f00;
  padding: 1rem;
  *zoom: 1;
  width: auto;
  max-width: 300;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 0;
  padding-right: 0;
}
.login:before,
.login:after {
  content: '';
  display: table;
}
.login:after {
  clear: both;
}
.dashboard {
  border: 1px solid #f00;
  width: 90%;
  margin: 0 auto;
  padding: 1rem;
}
.dashboard .logout {
  text-align: right;
  margin-bottom: 1rem;
}
.dashboard .addnew button {
  width: 100%;
  padding: 0.5rem 0;
}
.dashboard .current .item {
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(0,0,0,0.5);
}
.dashboard .current .name {
  text-transform: uppercase;
  *zoom: 1;
  float: left;
  clear: none;
  text-align: inherit;
  width: 82.83333333333334%;
  margin-left: 0%;
  margin-right: 3%;
}
.dashboard .current .name:before,
.dashboard .current .name:after {
  content: '';
  display: table;
}
.dashboard .current .name:after {
  clear: both;
}
.dashboard .current .name:nth-child(2n) {
  margin-right: 0%;
  float: right;
}
.dashboard .current .name:nth-child(2n+1) {
  clear: both;
}
.dashboard .current .price {
  *zoom: 1;
  float: left;
  clear: none;
  text-align: inherit;
  width: 14.166666666666664%;
  margin-left: 0%;
  margin-right: 3%;
}
.dashboard .current .price:before,
.dashboard .current .price:after {
  content: '';
  display: table;
}
.dashboard .current .price:after {
  clear: both;
}
.dashboard .current .price:nth-child(2n) {
  margin-right: 0%;
  float: right;
}
.dashboard .current .price:nth-child(2n+1) {
  clear: both;
}
