.menu {
  *zoom: 1;
  width: auto;
  max-width: 960px;
  float: none;
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-left: 0;
  padding-right: 0;
}
.menu:before,
.menu:after {
  content: '';
  display: table;
}
.menu:after {
  clear: both;
}
.menu header h2,
.menu header p {
  text-align: center;
}
.menu .name {
  text-transform: uppercase;
  *zoom: 1;
  float: left;
  clear: none;
  text-align: inherit;
  width: 82.83333333333334%;
  margin-left: 0%;
  margin-right: 3%;
}
.menu .name:before,
.menu .name:after {
  content: '';
  display: table;
}
.menu .name:after {
  clear: both;
}
.menu .name:nth-child(2n) {
  margin-right: 0%;
  float: right;
}
.menu .name:nth-child(2n+1) {
  clear: both;
}
.menu .price {
  *zoom: 1;
  float: left;
  clear: none;
  text-align: inherit;
  width: 14.166666666666664%;
  margin-left: 0%;
  margin-right: 3%;
}
.menu .price:before,
.menu .price:after {
  content: '';
  display: table;
}
.menu .price:after {
  clear: both;
}
.menu .price:nth-child(2n) {
  margin-right: 0%;
  float: right;
}
.menu .price:nth-child(2n+1) {
  clear: both;
}
